<template>
  <div style="max-width: 100%;box-sizing: border-box" class="needBalance">
    <myHeader
      :isBack="true"
      backPath="/finance"
      title="我要结算"
      subtitle="记录"
      subtitlePath="/needBalanceRecord">
    </myHeader>
    
    <div class="seat-head"></div>
    <div class="list-block">
      <mt-cell title="批次号：" @click.native="selectBatchNum">
        {{batchNumber}}
        <span class="img"></span>
      </mt-cell>
  
      <mt-cell title="申请日期：" @click.native="selectApplyDate">
        <span class="color-text">
          {{applyDateText}}
        </span>
        <span class="img"></span>
      </mt-cell>
      
      <mt-cell title="进苗头数：" :value="balanceData.bthcnt"></mt-cell>
      <mt-cell title="投苗日期：" :value="balanceData.bizdate"></mt-cell>
      <mt-cell title="清栏日期：" :value="balanceData.listdate"></mt-cell>
      <mt-cell title="上市正品数：" :value="balanceData.cfsszps"></mt-cell>
      <mt-cell title="上市均重（kg）：" :value="balanceData.shjz"></mt-cell>
      <mt-cell title="上市次品数：" :value="balanceData.cfsscps"></mt-cell>
      <mt-cell title="次品均重：" :value="balanceData.cpjz"></mt-cell>
      <mt-cell title="成活率（%）：" :value="balanceData.chl"></mt-cell>
      <mt-cell title="料肉比：" :value="balanceData.lrb"></mt-cell>
      <mt-cell title="上市日龄：" :value="balanceData.ssrl"></mt-cell>
    </div>
  
    <div class="my-prop">
      <!--选择批次-->
      <mt-popup
        v-model="switchBatchNumPopup"
        position="bottom">
        <mt-picker :slots="batchNumSlots"
                   value-key="name"
                   :show-toolbar="true"
                   ref="batchPicker">
          <span @click="handleBatchConfirm" class="sure-btn">确认</span>
        </mt-picker>
      </mt-popup>
    </div>
  
    <!--选择申请日期-->
    <mt-datetime-picker
      ref="applyBalanceDatePicker"
      type="date"
      v-model="applyDate"
      @confirm="applyDateConfirm"></mt-datetime-picker>

    <SubmitBtn @click.native="submitPageData"
               :btnText="submitBtnText"
               :customStyle="myCustomStyle"></SubmitBtn>
  </div>
</template>

<script>
  import myHeader from '@/components/Header'
  import {formatDate} from '@/utils/tool'
  import { balanceBatchNumberList ,initInfoByBatchId, saveBalanceRecord} from '@/api/materialApply'
  import SubmitBtn from "@/components/SubmitBtn";
  import { Indicator } from 'mint-ui'
  import subMitBtnMixins from '@/mixins/subMitBtnMixins'

export default {
  name: "needBalance",
  mixins: [subMitBtnMixins],
  data() {
    return {
      batchNumber: '',
      batchId: '',
      switchBatchNumPopup: false,
      batchNumSlots: [{
        values: [{
          id: '',
          name: ''
        }],
        textAlign: 'center'
      }],
      applyDateText: formatDate(new Date(), 'yyyy-MM-dd'),
      applyDate: new Date(),
      balanceData: {
        avgKg: '',
        bizdate: '',
        listdate: '',
        bthcnt: '',
        cfsscps: '',
        cfsszps: '',
        chl: '',
        cpjz: '',
        lrb: '',
        shjz: '',
        ssrl: '',
        zpjz: ''
      }
    };
  },
  components: {
    SubmitBtn,
    myHeader
  },
  created() {
    this._getBatchNumberList();
  },
  methods: {
    _getBatchNumberList() {
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      });
      balanceBatchNumberList().then((res) => {
        if (res.data && res.data.length) {
          this.batchNumSlots[0].values = res.data.map((item, index) => {
            return {
              name: item.name,
              id: item.id
            };
          });
  
          this.batchNumber = this.batchNumSlots[0].values[0].name;
          this.batchId = this.batchNumSlots[0].values[0].id;
          this._initInfoByBatchId(this.batchNumSlots[0].values[0].id);
        } else {
          this.isDisabledSubmit = true;
          Indicator.close();
        }
      
      }, err=> {
        this.isDisabledSubmit = true;
        Indicator.close();
      })
    },
    _initInfoByBatchId(batchId) {
      initInfoByBatchId(batchId).then(res => {
        Indicator.close();
        let data = res.data;
        if (data) {
          this.balanceData = {
            avgKg: data.avgKg,
            batchName: data.batchName,
            bizdate: data.bizdate,
            listdate: data.listdate,
            bthcnt: data.bthcnt,
            cfsscps: data.cfsscps,
            cfsszps: data.cfsszps,
            chl: data.chl,
            cpjz: data.cpjz,
            lrb: data.lrb,
            shjz: data.shjz,
            ssrl: data.ssrl,
            zpjz: data.zpjz
          }
        } else {
          this.isDisabledSubmit = true;
          this.$toast({
            message:  res.message[0].message,
            duration: 2000
          });
        }
    
      }).catch(err => {
        this.isDisabledSubmit = true;
        Indicator.close();
      })
    },
    selectBatchNum() {
      if (this.batchNumSlots[0].values[0].name) {
        this.switchBatchNumPopup = !this.switchBatchNumPopup;
      } else {
        this.$toast({
          message: '无可结算批次！',
          duration: 2000
        });
      }
    },
    handleBatchConfirm() {
      this.batchNumber = this.$refs.batchPicker.getValues()[0].name;
      this.batchId = this.$refs.batchPicker.getValues()[0].id;
      this.batchId ? this._initInfoByBatchId(this.batchId) : null;
      this.switchBatchNumPopup = false;
    },
    selectApplyDate() {
      this.$refs.applyBalanceDatePicker.open();
    },
    applyDateConfirm(value) {
      this.applyDateText = formatDate(value, 'yyyy-MM-dd');
    },
    submitPageData() {
      if (!this.isDisabledSubmit) {
        const data = {
          batchName: this.batchNumber,
          batchId: this.batchId,
          applyDate: this.applyDateText
        };
        
        this.isDisabledSubmit = true;
        this.submitBtnText = '提交中...';
  
        saveBalanceRecord(data).then(res => {
          this.$toast({
            message: '提交成功！',
            duration: 1000
          });
    
          this.$router.push('/needBalanceRecord');
          this.isDisabledSubmit = false;
          this.submitBtnText = '提交';
        }).catch(err => {
          this.isDisabledSubmit = false;
          this.submitBtnText = '提交';
        });
      }
    }
  }
};
</script>

<style lang="scss">
.needBalance {
  min-height: 100vh;
  background-color: #f8f8f8;

  .head {
    background-color: #fff;
  }

  .list-block {
    font-size: 0.16rem;
    margin-top: 0.1rem;
    background-color: #fff;

    .img {
      width: 0.1rem;
      height: 0.06rem;
      margin-left: 0.08rem;
      background: url("~@/assets/icon-next.png") no-repeat;
      background-size: 100%;
    }
  }

  .my-prop {
    .mint-popup {
      width: 100%;
      height: 25%;
    }
  
    /deep/ .picker-toolbar {
      height: 0;
    }
  
    .sure-btn {
      position: absolute;
      top: .05rem;
      right: .05rem;
      padding: .05rem .20rem;
      font-size: .16rem;
      z-index: 99;
    }
  }
  
}

</style>



